@use "variables" as v;
@use "mixins" as m;

html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  background: #fff;
}
body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: v.$blanco;
  font-family: v.$fuente_principal;
  font-size: 1.6rem;
  background-color: #181818;
  margin: 0;

  @include m.tablet {
    background: radial-gradient( circle 610px at 5.2% 51.6%,  rgba(5,8,114,1) 0%, rgba(7,3,53,1) 97.5% );

  }
}
*,
*:before,
*:after {
  box-sizing: inherit;
}
.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}
a {
  text-decoration: none;
}

img {
  max-width: 100%;
}
