@use "base/mixins" as m;
@use "base/variables" as v;

.listado-servicios {

  @include m.tablet {
    @include m.grid(2, 1rem);
  }
  @include m.desktop {
    @include m.grid(3, 1rem);
  }

  .servicio {
    background-color: v.$azul;
    text-align: center;
    border-radius: 1rem;
    margin: 1rem;
    padding: .4rem;
    transition: ease .3s transform, ease .5s background-color;
    
    &:hover{
      transform: scale(1.1);
    }
    .precio-servicio{
      font-weight: bold;
    }

    &.seleccionado{
      
      background-color: v.$blanco;
      p{
        color: v.$azul;
      }
    }
    
  }
}

