@use "base/variables" as v;
@use "base/mixins" as m;

.btn{
    @include m.boton(v.$azul,v.$blanco);
    
    &:last-of-type{
        margin-left: 1rem;
    }
    &.reserva{
        @include m.boton(v.$blanco,v.$azul);
        padding: 1rem;
        width: 100%;
    
    }
    &.eliminar{
        @include m.boton(v.$rojo,v.$blanco);
        margin: 0;
        padding: 1rem 3rem;
        width: 10rem;
        padding: 1rem 0;
    }
    &.actualizar{
        @include m.boton(v.$azul,v.$blanco);
        margin: 0;
        padding: 1rem 3rem;
        width: 10rem;
        padding: 1rem 0;
    }

    &.btnAdmin{
        @include m.boton(v.$blanco,v.$negro);
        padding: 1rem 3rem;
        border-radius: 0;
        border: solid 1px v.$negro;
        width: 100%;
        margin: 0;
        
        @include m.tablet{
            width: 20rem;
            
        }
        &:hover{
            background-color: v.$azul;
        }
    }
}