@use "base/variables" as v;
@use "base/mixins" as m;

.tabs {
  button {
    border: none;
    background-color: v.$azul;
    color: v.$blanco;
    padding: 1rem;
    display: block;
    width: 100%;
    margin: 0;
    text-transform: uppercase;
    font-weight: bold;
    transition: 0.3s ease background-color;
    z-index: 1;
    &:hover {
      cursor: pointer;
      background-color: darken(v.$azul, 10%);
    }
    &.actual {
      background-color: v.$blanco;
      color: v.$azul;
      // border: 1px v.$azul solid;
    }
  }
  @include m.tablet {
    display: flex;
  }
}
