@use "base/variables" as v;

.alerta {
  color: v.$rojo;
  &.error {
    color: v.$blanco;
    text-align: center;
    padding: 1rem;
    font-size: 2rem;
    font-weight: bold;
    background-color: v.$rojo;
    display: block;
    margin: 1rem;
    border-radius: 1rem;
  }
}

.exito {
  text-align: center;
  padding: 1rem;
  font-size: 2rem;
  font-weight: bold;
  background-color: v.$verde;
  display: block;
  margin: 1rem;
  border-radius: 1rem;
}
