@use "base/variables" as v;
@use "base/mixins" as m;

.contenedor-app {
  @include m.tablet {
    -webkit-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.34);
    -moz-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.34);
    box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.34);
  }

  background: #181818;
  overflow: hidden;

  @include m.tablet {
    border-radius: 2rem;
    margin: 2rem;
  }

  @include m.desktop {
    margin: 2rem 5rem;
  }
  @include m.desktop-large {
    margin: 2rem 10rem;
  }
}

.imagen {
  height: 30rem;
  width: auto;
  background-image: url(../img/2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.app {
  padding: 1rem;
  border: none;
  width: 90%;
  margin: 1rem auto;

  @include m.tablet {
    width: 80%;
  }
}

.smile {
  background-image: url(../img/smile.png);
  height: 8rem;
  width: 8rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.barra-servicios {
  margin: 2rem auto;
  
  @include m.tablet {
    display: flex;
    justify-content: center;
  }
}
