@use "base/variables" as v;
@use "base/mixins" as m;

ul {
  padding: 0;
}

.li-cita {
  background-color: v.$grisOscuro;
  border-radius: 1rem;
  list-style: none;
  padding: 2rem;
  display: flex;
  justify-content: space-between;

  margin: 1rem auto;
  p {
    color: v.$azul;
    margin: 0;

    span {
      color: v.$blanco;
      font-weight: bold;
    }
  }
  h3 {
    margin: 0;
    text-align: end;
  }
  color: v.$blanco;
  .servicio {
    text-align: end;
  }
  .contnedor-datos {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    .datos-cliente {
      margin: 0;
      p {
        margin: 0;
        text-align: left;
      }
    }
    form {
      margin: 2rem 0 0 0;
    }
  }
  .contenedor-servicios {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
  }
}

.total {
  background-color: v.$blanco;
  border-radius: 1rem;
  padding: 0.5rem;
  margin: 2rem 0 0 0;
  p {
    font-size: 3rem;
    color: v.$negro;
    text-align: center;
    margin: 0;
    span {
      font-size: 3rem;
      color: v.$negro;
      font-weight: bold;
    }
  }
}

.servicios {
  @include m.tablet {
    @include m.grid(2, 1rem);
  }
  @include m.desktop {
    @include m.grid(3, 1rem);
  }
  p {
    color: v.$azul;
    
    span {
      color: v.$blanco;
      font-weight: bold;
    }
  }
  li {
    padding: 1rem;
    list-style: none;
    border-radius: 1rem;
    background-color: v.$grisOscuro;
    margin: 1rem;
    @include m.tablet{
      margin: 0;
    }
  }
  .acciones{
    margin: 0;
    display: flex;
    justify-content: space-around;
  }
}
