@use 'base/variables' as v;
.barra{
    display: flex;
    justify-content: space-between;
    margin: 2rem;
    p{
        margin: 0;
        font-size: 2rem;
    }
    a{
        color: v.$azul;
        font-size: 2rem;
        font-weight: bold;
        &:hover{
            color: v.$rojo;
        }
    }
}