@use "base/variables" as v;

.text-center {
  text-align: center;
}

.carrousel {
  width: 100%;
}
.contenedor-secciones {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 300%;
  height: 0px;
  transition: all 0.5s ease;
  
  //transform: translateX(0%);
}
.seccion {
  overflow: hidden;
  width: calc(100% / 3);
  padding: 1rem;
}

#app {
  //border: 1px v.$azul solid;
  border-radius: 0 0 1rem 1rem;
  overflow: hidden;
}

.mostrar {
  display: block;
  animation: mymove 0.3s linear;
}

.ocultar {
  visibility: hidden;
}


