@use "base/variables" as v;
@use "base/mixins" as m;

.formulario {
  margin: 0 auto;
  width: 100%;
  @include m.tablet {
    width: 80%;
  }
}

.campo {
  display: flex;
  flex-direction: column;
  padding-bottom: 1rem;
  &:last-of-type {
    padding-bottom: 2rem;
  }

  label {
    text-align: center;
    padding: 1rem;
    font-size: 2rem;
    font-weight: bold;
    &.alerta {
      color: v.$rojo;
      font-weight: 300;
      font-size: 1.8rem;
      margin: 0;
      padding: 0;
    }
  }

  input {
    border-radius: 0.5rem;
    border: 1px solid #000;
    padding: 0.8rem;
    background-color: v.$grisOscuro;
    color: v.$blanco;
    &.bordeAlerta {
      border: 1px red solid;
    }
    &:disabled{
      cursor: not-allowed;
      background-color: lighten(v.$grisOscuro, 20);
    }
  }
}

.input-flex {
  @include m.tablet {
    display: flex;
    justify-content: space-around;
    .campo {
      width: 100%;
      &:first-of-type {
        padding-right: 1rem;
      }
    }
  }
}
