  box-sizing: border-box;
  height: 100%;
  background: #fff;
}

body {
  min-height: 100%;
  display: flex;
  flex-direction: column;
  color: #ffffff;
  font-family: "Roboto Condensed", sans-serif;
  font-size: 1.6rem;
  background-color: #181818;
  margin: 0;
}
@media (min-width: 768px) {
  body {
    background: radial-gradient(circle 610px at 5.2% 51.6%, rgb(5, 8, 114) 0%, rgb(7, 3, 53) 97.5%);
  }
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
}

h1, h2, h3 {
  font-family: "Roboto Condensed", sans-serif;
  margin: 0 0 2.5rem 0;
  font-weight: 900;
  color: #ffffff;
  text-align: center;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
  text-align: center;
}

p {
  color: #ffffff;
  font-size: 2rem;
}

.nombre-pagina {
  font-weight: bold;
}

.descripcion-pagina {
  text-align: center;
  font-size: 2.3rem;
}

@keyframes mymove {
  0% {
    transform: translatey(10px);
  }
  100% {
    transform: translatey(0px);
  }
}
.contenedor-app {
  background: #181818;
  overflow: hidden;
}
@media (min-width: 768px) {
  .contenedor-app {
    -webkit-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.34);
    -moz-box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.34);
    box-shadow: 0px 0px 23px -1px rgba(0, 0, 0, 0.34);
  }
}
@media (min-width: 768px) {
  .contenedor-app {
    border-radius: 2rem;
    margin: 2rem;
  }
}
@media (min-width: 1024px) {
  .contenedor-app {
    margin: 2rem 5rem;
  }
}
@media (min-width: 1366px) {
  .contenedor-app {
    margin: 2rem 10rem;
  }
}

.imagen {
  height: 30rem;
  width: auto;
  background-image: url(../img/2.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.app {
  padding: 1rem;
  border: none;
  width: 90%;
  margin: 1rem auto;
}
@media (min-width: 768px) {
  .app {
    width: 80%;
  }
}

.smile {
  background-image: url(../img/smile.png);
  height: 8rem;
  width: 8rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  margin: 0 auto;
}

.barra-servicios {
  margin: 2rem auto;
}
@media (min-width: 768px) {
  .barra-servicios {
    display: flex;
    justify-content: center;
  }
}

.text-center {
  text-align: center;
}

.carrousel {
  width: 100%;
}

.contenedor-secciones {
  display: flex;
  justify-content: space-evenly;
  align-items: flex-start;
  width: 300%;
  height: 0px;
  transition: all 0.5s ease;
}

.seccion {
  overflow: hidden;
  width: 33.3333333333%;
  padding: 1rem;
}

#app {
  border-radius: 0 0 1rem 1rem;
  overflow: hidden;
}

.mostrar {
  display: block;
  animation: mymove 0.3s linear;
}

.ocultar {
  visibility: hidden;
}

@media (min-width: 768px) {
  .listado-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .listado-servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.listado-servicios .servicio {
  background-color: #0066c0;
  text-align: center;
  border-radius: 1rem;
  margin: 1rem;
  padding: 0.4rem;
  transition: ease 0.3s transform, ease 0.5s background-color;
}
.listado-servicios .servicio:hover {
  transform: scale(1.1);
}
.listado-servicios .servicio .precio-servicio {
  font-weight: bold;
}
.listado-servicios .servicio.seleccionado {
  background-color: #ffffff;
}
.listado-servicios .servicio.seleccionado p {
  color: #0066c0;
}

.contenido-resumen {
  padding-bottom: 5rem;
}
.contenido-resumen p {
  color: #ffffff;
  margin-bottom: 1rem;
}
.contenido-resumen span {
  color: #0066c0;
  font-weight: bold;
}

.contendor-servicio {
  border-bottom: 1px solid #e1e1e1;
  padding: 0.3rem;
}
.contendor-servicio:last-of-type {
  margin-bottom: 3rem;
}

ul {
  padding: 0;
}

.li-cita {
  background-color: #292929;
  border-radius: 1rem;
  list-style: none;
  padding: 2rem;
  display: flex;
  justify-content: space-between;
  margin: 1rem auto;
  color: #ffffff;
}
.li-cita p {
  color: #0066c0;
  margin: 0;
}
.li-cita p span {
  color: #ffffff;
  font-weight: bold;
}
.li-cita h3 {
  margin: 0;
  text-align: end;
}
.li-cita .servicio {
  text-align: end;
}
.li-cita .contnedor-datos {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.li-cita .contnedor-datos .datos-cliente {
  margin: 0;
}
.li-cita .contnedor-datos .datos-cliente p {
  margin: 0;
  text-align: left;
}
.li-cita .contnedor-datos form {
  margin: 2rem 0 0 0;
}
.li-cita .contenedor-servicios {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.total {
  background-color: #ffffff;
  border-radius: 1rem;
  padding: 0.5rem;
  margin: 2rem 0 0 0;
}
.total p {
  font-size: 3rem;
  color: #181818;
  text-align: center;
  margin: 0;
}
.total p span {
  font-size: 3rem;
  color: #181818;
  font-weight: bold;
}

@media (min-width: 768px) {
  .servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 1024px) {
  .servicios {
    display: grid;
    gap: 1rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.servicios p {
  color: #0066c0;
}
.servicios p span {
  color: #ffffff;
  font-weight: bold;
}
.servicios li {
  padding: 1rem;
  list-style: none;
  border-radius: 1rem;
  background-color: #292929;
  margin: 1rem;
}
@media (min-width: 768px) {
  .servicios li {
    margin: 0;
  }
}
.servicios .acciones {
  margin: 0;
  display: flex;
  justify-content: space-around;
}

.formulario {
  margin: 0 auto;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario {
    width: 80%;
  }
}

.campo {
  display: flex;
  flex-direction: column;
  padding-bottom: 1rem;
}
.campo:last-of-type {
  padding-bottom: 2rem;
}
.campo label {
  text-align: center;
  padding: 1rem;
  font-size: 2rem;
  font-weight: bold;
}
.campo label.alerta {
  color: #e00000;
  font-weight: 300;
  font-size: 1.8rem;
  margin: 0;
  padding: 0;
}
.campo input {
  border-radius: 0.5rem;
  border: 1px solid #000;
  padding: 0.8rem;
  background-color: #292929;
  color: #ffffff;
}
.campo input.bordeAlerta {
  border: 1px red solid;
}
.campo input:disabled {
  cursor: not-allowed;
  background-color: #5c5c5c;
}

@media (min-width: 768px) {
  .input-flex {
    display: flex;
    justify-content: space-around;
  }
  .input-flex .campo {
    width: 100%;
  }
  .input-flex .campo:first-of-type {
    padding-right: 1rem;
  }
}

