.acciones {
  margin-top: 3rem;
  text-align: center;
}
.acciones a {
  display: block;
  padding: 1rem;
  color: #ffffff;
}

.alerta {
  color: #e00000;
}
.alerta.error {
  color: #ffffff;
  text-align: center;
  padding: 1rem;
  font-size: 2rem;
  font-weight: bold;
  background-color: #e00000;
  display: block;
  margin: 1rem;
  border-radius: 1rem;
}

.exito {
  text-align: center;
  padding: 1rem;
  font-size: 2rem;
  font-weight: bold;
  background-color: #329f00;
  display: block;
  margin: 1rem;
  border-radius: 1rem;
}

.tabs button {
  border: none;
  background-color: #0066c0;
  color: #ffffff;
  padding: 1rem;
  display: block;
  width: 100%;
  margin: 0;
  text-transform: uppercase;
  font-weight: bold;
  transition: 0.3s ease background-color;
  z-index: 1;
}
.tabs button:hover {
  cursor: pointer;
  background-color: #004b8d;
}
.tabs button.actual {
  background-color: #ffffff;
  color: #0066c0;
}
@media (min-width: 768px) {
  .tabs {
    display: flex;
  }
}

.paginacion {
  padding: 1rem 0;
  display: flex;
  justify-content: space-between;
  background: #181818;
  z-index: 1;
