}

.barra {
  display: flex;
  justify-content: space-between;
  margin: 2rem;
}
.barra p {
  margin: 0;
  font-size: 2rem;
}
.barra a {
  color: #0066c0;
  font-size: 2rem;
  font-weight: bold;
}
.barra a:hover {
  color: #e00000;
}